import React from 'react';
import ReactDOM from 'react-dom';
import Post from './Post';
import GitTmpl from './git.rt';
import Flow from './Flow';

class Git extends Post {
	render() {
		return (
		<div>
			<div className="article">
				<div className="page_margins">	
				<Flow></Flow>
				</div>
			</div>
		</div>
		);
	}
}

export default Git;


