import React from "react";
import {Link} from 'react-router-dom';
import logoTemplate from './logo.rt';

const NavigationMenu = () => (
	<div id="header">
		<div className="page_margins">
		  <h1>logo</h1>
		  <h2>Prime Theorem</h2>
		  <div className="align-text-center">
					<span className="blink home-banner-text">TECHNOLOGY</span>
					<span className="banner"><span className="banner-text">A<span id="offset">R</span>T</span></span>
					<span className="blink home-banner-text">DESIGN</span>
		  </div>
		  <div id="service-nav">
			<ul>
			  <li><Link to="/">Home</Link></li>
			  <li><Link to="/myspace">My Space</Link></li>
			  <li><Link to="/tech">Tech</Link></li>
			  <li><Link to="/art">Art</Link></li>
			  <li><Link to="/design">Design</Link></li>
			  <li><Link to="/contact">Contact</Link></li>
			</ul>
		  </div>
		</div>
	</div>
);

export default NavigationMenu;


