import React from "react";
import {Switch,Route} from 'react-router-dom'
import Home from './Home';
import MySpace from './MySpace';
import About from './About';
import Contact from './Contact';
import MachineLearning from './MachineLearning';
import MicroserviceSize from './MicroserviceSize';
import TechBlog from './TechBlog';
import TestableCode from './TestableCode';
import ThreatModeling from './ThreatModeling';
import Git from './Git';

const Routes = () => (
	<Switch>
      <Route exact path="/" component={Home}/>
	  <Route exact path="/index.html" component={Home}/>
	  <Route exact path="/myspace" component={MySpace}/>
	  <Route exact path="/tech" component={TechBlog}/>
	  <Route exact path="/maclearn" component={MachineLearning}/>
	  <Route exact path="/testablecode" component={TestableCode}/>
	  <Route exact path="/art" component={Home}/>
	  <Route exact path="/design" component={Home}/>
	  <Route exact path="/contact" component={Contact}/>
	  <Route exact path="/mssize" component={MicroserviceSize}/>
	  <Route exact path="/collaborativecoding" component={MicroserviceSize}/>
	  <Route exact path="/threatmodeling" component={ThreatModeling}/>
	  <Route exact path="/git" component={Git}/>
	</Switch>
);

export default Routes;



