import React from 'react';
import {Link} from 'react-router-dom';
import TechBlogTmpl from './techBlog.rt';

class TechBlog extends React.Component {
	render() {
		return (
		<div>
			{TechBlogTmpl()}
			<div className="article">
				<div className="page_margins">
				  <div className="two_col">
					  <ul className="rectangle-list">
						<li><Link to="/testablecode">Testable Code</Link></li>
						<li><Link to="/maclearn">Machine Learning</Link></li>   
						<li><Link to="/threatmodeling">Threat Modeling</Link></li>
						<li><Link to="/git">GIT</Link></li>						
					  </ul>
				  </div>	
				</div>
			</div>
		</div>
		);
	}
}

export default TechBlog;


