import React from "react";
import NavigationMenu from './NavigationMenu';
import Routes from './Routes';
import TimeLineTemplate from './timeLine.rt';

const App = () => (
  <div>
	<div className="header navbar-fixed-top">
		<NavigationMenu/>
	</div>
	<Routes/>	
	{TimeLineTemplate()}
  </div>
);

export default App;



