import React from 'react';
import ReactDOM from 'react-dom';
import Post from './Post';
import TestableCodeTmpl from './testableCode.rt';

class TestableCode extends Post {
	render() {
		return (
		<div>
			{TestableCodeTmpl()}
		</div>
		);
	}
}

export default TestableCode;


