import React from 'react';
import ReactDOM from 'react-dom';

const Start = (props) => {
	let xPos = props.xPos;
	let yPos = props.yPos;
	const divStyle = {
		position: 'absolute',
		width:props.nodeWidth,
        height:"40px",
		background:"rgb(77, 161, 247)",
		borderRadius:"40px"
	};
	
	divStyle.left=xPos;
	divStyle.top=yPos;
	
  return (
			<div style={divStyle}></div>
		);
}
 
export default Start;


