import React from 'react';
import ReactDOM from 'react-dom';
import Post from './Post';
import ThreatModelingTmpl from './threatModeling.rt';

class TestableCode extends Post {
	render() {
		return (
		<div>
			{ThreatModelingTmpl()}
		</div>
		);
	}
}

export default TestableCode;


