import React from 'react';
import ReactDOM from 'react-dom';

const Decision = (props) => {
	let xPos = props.xPos;
	let yPos = props.yPos;
	const divStyle = {
		position: 'absolute',
		width:props.nodeWidth,
		height: 100,
		backgroundColor: "#4DABF7"/*,
		transform: 'rotate(45deg)'*/
	};
	
	divStyle.left=xPos;
	divStyle.top=yPos;
	
	return (
		<div style={divStyle}></div>
	);
}
 
export default Decision;


