import React from 'react';
import ReactDOM from 'react-dom';

const Process = (props) => {
	let xPos = props.xPos;
	let yPos = props.yPos;
	const divStyle = {
		position: 'absolute',
		width:props.nodeWidth,
        height:"60px",
		background:"#4DABF7",
		float:'left'
	};
	divStyle.left=xPos;
	divStyle.top=yPos;
	return (
		<div style={divStyle}></div>
	);
}
 
export default Process;


