import React from 'react';
import ReactDOM from 'react-dom';
import Post from './Post';
import MachineLearningTmpl from './machineLearning.rt';
import LinearRegressionTmpl from './linearRegression.rt';
import CollaborativeFilteringTmpl from './collaborativeFiltering.rt';

class MachineLearning extends Post {
	render() {
		return (
		<div>
			{MachineLearningTmpl()}
			{CollaborativeFilteringTmpl()}
			{LinearRegressionTmpl()}
		</div>
		);
	}
}

export default MachineLearning;


