import React from 'react';
import ReactDOM from 'react-dom';
import Post from './Post';
import MicroserviceSizeTmpl from './microserviceSize.rt';

class MicroserviceSize extends Post {
	render() {
		return (
		<div>
			{MicroserviceSizeTmpl()}
		</div>
		);
	}
}

export default MicroserviceSize;


