import React from 'react';
import {Link} from 'react-router-dom';
import MyTechnologyTmpl from './myTechnology.rt';

class MySpace extends React.Component {
	render() {
		return (
		<div>
			{MyTechnologyTmpl()}
			<div className="article">
				<div className="page_margins">
				  <div className="two_col">
					  <ul className="rectangle-list">
						<li><Link to="/mssize">How Small Should Microservices Be?</Link></li>
						<li><Link to="/exflow">Execution Flow Graph</Link></li>                     
					  </ul>
				  </div>	
				</div>
			</div>
		</div>
		);
	}
}

export default MySpace;


