import React from 'react';
import ReactDOM from 'react-dom';
import Post from './Post';
import MyTechnology from './MyTechnology';
import MyArtTmpl from './myArt.rt';
import MyDesignTmpl from './myDesign.rt';

class MySpace extends Post {
	render() {
		return (
		<div>
			<MyTechnology></MyTechnology>
			{MyArtTmpl()}
			{MyDesignTmpl()}
		</div>
		);
	}
}

export default MySpace;


